@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
body {
  padding: 0;
  margin: 0;
  background: #000;
  background-image: url('http://aradesign.in/content/common/bg-image.png');
}
.main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}
.main-container .row {
  width: 100%;
}
.main-container .row .container {
  width: 100%;
}
.main-container .row .container .grid {
  display: flex;
}
.main-container .row .container .grid .column.column-right {
  margin-left: auto;
}
.main-container .row .container .grid .column.column-right .menu img {
  width: 25px;
}
.main-container .super-container.people-details .people-list {
  height: 100vh;
  display: flex;
  align-items: center;
}
.main-container .super-container .row .container.team-container {
  width: 100%;
  padding: 50px 0;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.team-container {
    padding: 50px 30px;
  }
}
.main-container .super-container .row .container.team-container .grid {
  display: block !important;
}
.main-container .super-container .row .container.team-container .grid .column {
  padding: 15px 18px;
}
.main-container .super-container .row .container.team-container .grid .column a {
  text-decoration: none;
}
.main-container .super-container .row .container.team-container .grid .column .image-wrapper img {
  width: 100%;
}
.main-container .super-container .row .container.team-container .grid .column .details-section h2 {
  font-size: 18px;
  color: #fff;
  font-weight: 700;
  line-height: 21px;
  padding: 15px 0;
  margin: 0;
}
.main-container .super-container .row .container.team-container .grid .column .details-section p {
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  line-height: 16px;
  margin: 0;
}
