body {
  padding: 0;
  margin: 0;
  background: #000;
  background-image: url('http://aradesign.in/content/common/bg-image.png');
}
.main-container {
  height: 100vh;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}
.main-container .row {
  width: 100%;
}
.main-container .row .container {
  width: 100%;
}
.main-container .row .container .grid {
  display: flex;
}
.main-container .row .container .grid .column.text-content {
  display: flex;
  align-items: flex-end;
}
.main-container .row .container .grid .column.column-right {
  margin-left: auto;
}
.main-container .row .container .grid .column.column-right .menu img {
  width: 25px;
}
.main-container .super-container.people-details .people-row {
  width: 100%;
  display: table;
  margin: 0;
  max-width: none;
  height: 80vh;
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container.people-details .people-row {
    height: 100%;
  }
}
.main-container .super-container .row .container.people-container {
  position: relative;
}
.main-container .super-container .row .container.people-container .scroll-text {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-bottom: -25px;
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  line-height: 28px;
}
.main-container .super-container .row .container.people-container .container-arrow__arrow {
  pointer-events: none;
  overflow: hidden;
  position: absolute;
  right: auto;
  bottom: 0;
  height: 50px;
  z-index: 2;
  width: 100%;
  margin-bottom: -70px;
}
.main-container .super-container .row .container.people-container .container-arrow__arrow::before {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: 15px;
  width: 13px;
  height: 78px;
  background-repeat: no-repeat;
  background: url(http://aradesign.in/content/common/scroll.png);
  -webkit-animation: screen-arrow 1.9s infinite ease;
  animation: screen-arrow 1.9s infinite ease;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.people-container .grid {
    display: flex;
    flex-direction: column-reverse;
  }
}
.main-container .super-container .row .container.people-container .grid .column-right {
  width: 65%;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.people-container .grid .column-right {
    width: 100%;
  }
}
.main-container .super-container .row .container.people-container .grid .column-right .slider-section .slick-dots {
  bottom: 0;
  margin-bottom: 10px;
  text-align: right;
}
.main-container .super-container .row .container.people-container .grid .column-right .slider-section .slick-dots li button {
  margin-right: 0px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 16px solid #4d4d4d;
  transition: all 0.5s ease-in-out;
  border-radius: 2px;
  padding: 0;
}
.main-container .super-container .row .container.people-container .grid .column-right .slider-section .slick-dots li button:hover {
  border-bottom: 16px solid #fff;
}
.main-container .super-container .row .container.people-container .grid .column-right .slider-section .slick-dots li button::before {
  width: 0;
  color: transparent;
}
.main-container .super-container .row .container.people-container .grid .column-right .slider-section .slick-dots li.slick-active button {
  border-bottom: 16px solid #fff;
}
.main-container .super-container .row .container.people-container .grid .column-right img {
  width: 100%;
}
.main-container .super-container .row .container.people-container .left-content {
  margin-bottom: 25px;
  padding-right: 25px;
  position: static !important;
}
@media only screen and (max-width: 768px) {
  .main-container .super-container .row .container.people-container .left-content {
    padding: 30px 0;
  }
}
.main-container .super-container .row .container.people-container .left-content h2 {
  font-size: 38px;
  color: #fff;
  font-weight: 800;
  line-height: 52px;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.people-container .left-content h2 {
    font-size: 38px;
    line-height: 37px;
  }
}
@media only screen and (max-width: 375px) {
  .main-container .super-container .row .container.people-container .left-content h2 {
    font-size: 32px;
    line-height: 37px;
  }
}
.main-container .super-container .row .container.people-container .left-content p {
  font-size: 18px;
  color: #fff;
  font-weight: 400;
  line-height: 28px;
  margin: 0;
  padding: 20px 0 0;
  font-family: 'Montserrat', sans-serif;
}
@media only screen and (max-width: 558px) {
  .main-container .super-container .row .container.people-container .left-content p {
    font-size: 14px;
    line-height: 18px;
    padding: 20px 0;
    width: 100%;
  }
}
@-webkit-keyframes screen-arrow {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
@keyframes screen-arrow {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
